@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.loading{
    margin-top: 3em;
    display: flex;
    flex-direction: row;
    justify-content: center;
    
    

}

.loading_msg{
    display: flex;
    flex-direction: row;
    grid-gap: 2em;
    gap: 2em;
    align-items: center;
    font-size: 2em;
    height: 2em;
}
.missions-table{
    display: flex;
    width: 100vw;
    justify-content: end;
    padding-right: 2vw;

}
.missions{
    display: flex;
    width: 76.5%;
    flex-direction: column;
}

.mission-header{
    display: grid;
    grid-template-columns:  3.6fr 2.6fr 1.5fr 1.5fr 0.525fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.9em;   
}

.thin_ruler{
    width: 95%;
    display: grid;
    grid-template-columns:  3.9fr 6.1fr;
    background-color: var(--void);
    height: 0.05em;
    align-content: center;
    margin-top: -0.5em;

}
.fat_ruler{
    height: 0.4em;
    background-color: var(--void);
    border-radius: 1em;
}
.mission{
    display: grid;
    grid-template-columns: 3.6fr 2.6fr 1.5fr 1.3fr 0.525fr;
    grid-gap: 1.2em;
    gap: 1.2em;
    margin-top: 0.5em;    
    align-items: center;
    color: var(--moon-dust);
}

.mission-header-cell{
    width: 100%;
    padding-top: 0.6em;
    padding-bottom: 0.6em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.2em;
}
.column-title{
    cursor: pointer;
}

.cell{
    background-color: var(--starlight);
    width: 100%;
    padding-top: 1.6em;
    padding-bottom: 1.6em;
    padding-left: 1em;
    border-radius: 1em;
    margin: 0.1em;
    align-self: stretch;
    justify-items: stretch;
}
.center{
    text-align: center;
}

.load-more{
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    flex-direction: row;
    height: 1em;
    padding-left: 1em;
    margin-top: 1em;
}
.page{
    font-weight: bold;
}
.mission:hover  .cell{
    background-color: var(--martian-soil);
    color: var(--white-dwarf);
    transition: 0.2s;
}


.next-icon{
    height:2em;
    justify-self: center;
    display: none;
}
.mission:hover .next-icon{
    display: grid;
}


@media (max-width: 600px) {

    .missions-table{
        justify-content: flex-start;
        margin-left: 1em;
        margin-top: 2em;
        width: 100%;
        transform: scale(0.8);
    }
    .missions{
        width: 100%;
    }
    .mission-header{
        transform: scale(0.8);
        grid-template-columns: 3.6fr 2.6fr 1.5fr 1.3fr;
    }
  }

  @media (max-width: 400px) {
    .missions-table{
        margin-left: 1em;
        margin-top: 2em;
        width: 100%;
        transform: scaleX(0.8);
    }

    
  }

  @media (min-width: 1024px) {
    .missions-table{
        font-size: large;     
    }
    
  }

  @media (min-width: 2000px) {
    .missions-table{
        font-size: x-large;
    }
    
  }
  @media (min-width: 3000px) {
    .missions-table{
        font-size: xx-large;  
    }
  }


.arrow{
    display: none;
    height:1em;
    padding-left: 1em;
}
.visible{
    display: inline-block;
}

.arrow-up{
    transition: 0.5s;
}

.arrow-down{
    transform: rotate(180deg);
    transform-origin: center;
    transition: 0.5s;
}
.ticket-wrapper{
    position: absolute;
    width: 100%;
    height: 100vh;
    background-image: url(/react-coding-exercise/static/media/ticketPageBackground.33ea2af8.svg);
    background-repeat: no-repeat;
    background-size: cover;
    overflow-x: hidden;
    font-weight: 300;
}
.wrapper-2{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-content: flex-end;
    align-items: center;
}
.wrapper-3{
    padding-top: 2em;
    display: flex;
    flex-direction: row;
    align-content: flex-start;

}
.back{
    padding-top: 2em;
    cursor: pointer;
}
.ticket{
    width: 60em;
    height: 25em;
    background-image: url(/react-coding-exercise/static/media/ticketBackground.7393475e.svg);
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 100%;
    color: var(--distant-star);
}

.mission_details{
    display: flex;
    flex-direction: column;
}
.year_wrapper{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-content: flex-start;
    align-items: flex-start;
    line-height: 0.1em;
    color:var(--void);    
    padding-top: 4em;
    padding-right: 8.2em;

}

.mission_name{
    margin-top: -2em;
    padding-left: 8.5em;
    line-height: 0.5em;
}

.rocket_details{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-left: 8.5em;
    grid-gap: 3em;
    gap: 3em;
    line-height: 0.3em;
}


.print{
    z-index: 10;
    display: flex;
    width: 55em;
    justify-content: flex-end;
}

.name, .year, .r_name, .r_type{
    font-weight: 300;
}

@media (max-width: 1050px) {
    .ticket{ 
        transform: rotate(90deg); 
        margin-top: 16em;
    }

    .wrapper-3{
        flex-direction: column;
        align-items: center;
    }
    .print{
        justify-content: center;
        margin-top: 14em;
    }
    
  }

  @media (min-width: 1900px){
    .wrapper-3{
        transform: scale(1.5);
        margin-top: 5em;
    }
    .print{
        transform: scale(1.5);
        margin-top: 6em;
    }
  }

  @media (min-width: 2500px){
    .wrapper-3{
        transform: scale(2);
        margin-top: 10em;
    }
    .print{
        transform: scale(2);
        margin-top: 12em;
    }
  }

  @media (min-width: 3800px){
    .wrapper-3{
        transform: scale(3);
        margin-top: 20em;
    }
    .print{
        transform: scale(3);
        margin-top: 24em;
    }
  }


:root {
    --martian-soil: rgb(209, 75, 57);
    --void: rgb(25, 50, 71);
    --distant-star: rgb(244, 221, 181);
    --white-dwarf: rgb(255, 255, 255);
    --starlight: rgb(253, 242, 221);
    --moon-dust: rgb(96, 96, 96);
    width: 100vw;
  }
  /* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  overflow-x: hidden;
  margin: 0;
}

  body{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgb(244, 221, 181);
    background-color: var(--distant-star);
    font-family: 'Roboto', sans-serif;
    color:rgb(25, 50, 71);
    color:var(--void);
    background-image: url(/react-coding-exercise/static/media/takeoff.31a44e1b.svg);
    background-repeat: no-repeat;
    background-size: 100%;
    margin-bottom: 5em;
  }

  .btn-primary{
    display: block;
    background-color: rgb(209, 75, 57);
    background-color: var(--martian-soil);
    color: rgb(255, 255, 255);
    color: var(--white-dwarf);
    text-align: center;
    border-radius: 2em;
    border-color: transparent;
    text-transform: uppercase;
    font-weight: bold;
    padding-left: 2em;
    padding-right: 2em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    cursor: pointer;
  }
.header-wrapper{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding-right: 2vw;
}
  .header{
    width: 76.5%;
    height: 16vh;
    display: grid;
    grid-template-columns: 3.6fr 4.2fr 1.2fr 0.525fr;
    grid-gap: 0.5em;
    gap: 0.5em;
    margin-top: 0.5em;    
    align-items: flex-end;
    justify-content: flex-end;
    align-content: flex-end;
  }

  .search_input{
    background-image: url(/react-coding-exercise/static/media/rocket.a663dc2a.svg);
    background-repeat: no-repeat;
    background-position-x: 0.5em;
    background-position-y: center;
    background-size: em;
    height: 2em;
    border-color: transparent;
    border-radius: 2em;
    background-color: rgb(25, 50, 71);
    background-color: var(--void);
    color: rgb(253, 242, 221);
    color: var(--starlight);
    padding-left: 3em;
  }
  .search_input::-webkit-input-placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .search_input:-ms-input-placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .search_input::placeholder{
    color: rgb(253, 242, 221);
    color: var(--starlight);
  }
  .logo{
    width: 100%;
  }
  a{
    text-decoration: none;
  }


  @media (max-width: 600px) {

    .header {
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-columns: 1fr;
      width: 100%;
      padding-left: 30%;
      margin-top: 2em;
      padding-right: 2em;
      align-content: center;
    }
  }

  @media (min-width: 1024px) {
    .header-wrapper{
        font-size: large;     
    }
    .search_input, .btn-primary{
      font-size: large;
    }
    
  }

  @media (min-width: 2000px) {
    .header-wrapper{
        font-size: x-large;     
    }
    .search_input, .btn-primary{
      font-size: x-large;
    }
    
  }
  @media (min-width: 3000px) {
    .header-wrapper{
        font-size: xx-large;     
    }
    .search_input, .btn-primary{
      font-size: xx-large;
    }    
  }

